import React, { Component } from 'react'
import { observer } from 'mobx-react';
import { HashRouter } from 'react-router-dom'

import Headers from './head'
import Bar from './bar'
import Content from './content'


@observer
export default class App extends Component {
  render() {
    let { props } = this;

    return (
      <HashRouter>
        <div>
          <Headers />
          <Bar />
          <Content />
        </div>
      </HashRouter>
    )
  }
}




